document.addEventListener('DOMContentLoaded', function() {
    iniciarApp();
});

window.addEventListener('scroll', function() {
    let mov = document.querySelector('.mov');
    let posicionImg = mov.getBoundingClientRect().top;
    let tamañoDePantalla = window.innerHeight / 2;

    if (posicionImg < tamañoDePantalla) {
        mov.style.animation = 'movs 1s ease-out'
    }
});


function iniciarApp() {
    navegacionFija();
    crearGaleria();
    crearSegundaGaleria();
    scrollNav();
}

function navegacionFija() {
    const barra = document.querySelector('.navegacion-principal');
    const sobre_projects = document.querySelector('.sobre-projects');

    window.addEventListener('scroll', function() {
        if (sobre_projects.getBoundingClientRect().bottom < 0) {
            barra.classList.add('fijo');
        } else {
            barra.classList.remove('fijo');
        }
    });
}

function scrollNav() {
    const enlaces = document.querySelectorAll('.navegacion-principal a');
    enlaces.forEach(enlace => {
        enlace.addEventListener('click', function(e) {
            e.preventDefault();
            const seccionScroll = e.target.attributes.href.value;
            const seccion = document.querySelector(seccionScroll);
            seccion.scrollIntoView({ behavior: "smooth" });
        });
    });
}

function crearGaleria() {
    const galeria = document.querySelector('.habilidades');

    for (let i = 1; i <= 4; i++) {
        const image = document.createElement('picture');
        image.innerHTML = `
        <source srcset="build/img/${i}.avif">   
        <source srcset="build/img/${i}.webp">
        <img loading="lazy" width="200" height="300" src="build/img/${i}.png" alt="IMAGE SKILLS">
        `;

        galeria.appendChild(image);

    }
}

function crearSegundaGaleria() {
    const segundaGaleria = document.querySelector('.habilidades-segundas');

    for (let i = 5; i <= 7; i++) {
        const imagen = document.createElement('picture');
        imagen.innerHTML = `
        <source srcset="build/img/${i}.avif">   
        <source srcset="build/img/${i}.webp">
        <img  loading="lazy" width="200" height="300" src="build/img/${i}.png" alt="IMAGE SKILLS">
        `;

        segundaGaleria.appendChild(imagen);
    }
}